.App {
  text-align: center;
}
.flex-column{
  align-items: center;
}
.teamPicture{
  vertical-align: top;
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
}
.row{
  justify-content:center;
  align-items: center;
  display: flex;
  align-content: space-between;
  text-align: center;
  margin: 50px;
}

.footer{
  max-width: 100%;
  height: 50px;
  bottom: 0;
  width: 100%;
  background-color: #F8F9FA;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
